* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  position: relative;
}

.Select {
  position: absolute;
  top: 50%;
  margin-top: -100px;
  left: 50%;
  margin-left: -100px;
  font-family: "Quicksand", sans-serif;
  width: 200px;
  font-size: 23px;
  border: 1px solid #000;
  cursor: pointer;
}

.Show {
  display: block;
}

.Hide {
  display: none;
}

.Selected {
  position: relative;
  border: 1px solid #000;
  padding-left: 5px;
  font-weight: bold;
}

.Select__item {
  padding-left: 5px;
}

.Select__item:hover {
  background-color: rgba(0, 0, 0, 0.25);
  color: #fff;
}

.Select__footer {
  border-top: 1px solid rgba(0, 0, 0, 0.195);
}

i {
  position: absolute;
  top: 10px;
  right: 10px;
  border: solid black;
  border-width: 0 3px 3px 0;
  margin-top: 0;
  display: inline-block;
  padding: 3px;
}

.up {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
